<template>
    <div class="photoinfo-container">
        <!-- 标题 -->
        <h3 class="title">{{photoinfo.title}}</h3>
        <p class="subtitle">
            <span>发表时间：{{photoinfo.add_time | dateFormat}}</span>
            <span>阅读：{{photoinfo.click}}次</span>
        </p>
        <hr>
        <!-- 缩略图区域 -->
        <vue-preview :slides="list" @close="handleClose" class="imgs"></vue-preview>
        <!-- 内容 -->
        <div class="content" v-html="photoinfo.content"></div>
        <!-- 评论 -->
        <comment :id='this.$route.params.id'/>
    </div>
</template>
<script>
import { Toast } from 'mint-ui'
import api from '@/api/index.js'
import Comment from '@/components/subcomponents/comment'

export default {
    data(){
        return {
            photoinfo:{},//图片文字信息
            list: [],//缩略图数组
             slide1: [
                {
                    src: 'https://farm6.staticflickr.com/5591/15008867125_68a8ed88cc_b.jpg',
                    msrc: 'https://farm6.staticflickr.com/5591/15008867125_68a8ed88cc_m.jpg',
                    alt: 'picture1',
                    title: 'Image Caption 1',
                    w: 600,
                    h: 400
                },
                {
                    src: 'https://farm4.staticflickr.com/3902/14985871946_86abb8c56f_b.jpg',
                    msrc: 'https://farm4.staticflickr.com/3902/14985871946_86abb8c56f_m.jpg',
                    alt: 'picture2',
                    title: 'Image Caption 2',
                    w: 1200,
                    h: 900
                }
            ]
        }
    },
    created() {
        const id = this.$route.params.id;
        this.getPhotoInfo(id);
        this.getThumimages(id);
    },
    methods: {
         handleClose () {
            console.log('close event')
        },
        //获取图片详情
        getPhotoInfo(id) {
            api.getPhotoInfo(id).then(res => {
                // console.log('9999',res);
                if(res.data.status === 0) {
                    this.photoinfo = res.data.message[0];
                } else {
                    Toast('请求图片详情失败！');
                }
            })
        },
        //获取图片缩略图数组
        getThumimages(id) {
            api.getThumimages(id).then(res => {
                if(res.data.status === 0) {
                    //询函每个图片，补全图片的宽和高
                    res.data.message.forEach(item => {
                        item.w= 600;
                        item.h = 400;
                        item.msrc= item.src;
                        item.alt= item.src;
                        item.title='Image Caption 1';
                    });
                    //把完整的数据保存到list中
                    this.list = res.data.message;
                    console.log('dfsfsdf',this.list);
                }
            })
        }
    },
    components:{
        Comment
    }
}
</script>

<style lang="scss">
.photoinfo-container{
    padding:0 4px;
    .title{
        font-size: 14px;
        text-align: center;
        margin:15px 0;
        color:#226aff;
    }
    .subtitle{
        font-size: 13px;
        color:#999;
        display: flex;
        justify-content: space-between;
    }
    .imgs{
        .my-gallery{
            display:flex;
            justify-content:space-between;
            flex-wrap: wrap;
            figure{
                display:inline-block;
                margin:0px;
                margin-bottom:10px;
                img{
                    width:110px;
                    height:110px;
                    box-shadow:0 0 8px #999;
                }
            }
        }
        
        
    }
    .content{
        img{
            width: 100%;
        }
    }
}

</style>



